export default {
  props: ["csvd"],
  data() {
    return {
      addPermModel: false,
      valid: false,
      snackbar: false,
      color: "",
      text: "",
      loading: false,
    };
  },
  methods: {
    async submit() {
      if (this.$refs.form.validate()) {
        this.valid = true;
        this.loading = true;
        try {
          await this.$axios
            .delete("api/admin/csvupload/" + this.csvd.id)
            .then((res) => {
              this.loading = false;
              this.color = "success";
              this.snackbar = true;
              this.text = res.data.message;
              this.$store.dispatch("csvdata/getCustomercsv");
              this.$refs.form.reset();
              this.addPermModel = false;
            });
        } catch (err) {
          this.loading = false;
          this.color = "error";
          this.snackbar = true;
          this.text = err.response.data.message;
        }
      }
    },
  },
};
