export default {
  layout: "user",
  name: "ImportUsers",
  async fetch() {
    addPermModel: false;
  },
  data() {
    return {
      addPermModel: false,
      valid: false,
      loading: false,
      snackbar: false,
      color: "",
      text: "",
      file: null,
      form:{
        title:''
      },
    };
  },
  computed: {},
  methods: {
    async submit() {
      if (this.$refs.form.validate()) {
        this.loading = true;
        this.valid = true;
        try {
        const formdata = new FormData();
        formdata.append("title", this.form.title);
        formdata.append("file", this.file);

        await this.$axios
          .post("api/admin/csvupload", formdata)
          .then((res) => {
              this.color="success"
             this.snackbar=true
            this.text=res.data.message
             this.$store.dispatch('csvdata/getCustomercsv') 
             // this.$router.push('dashboard')
             this.$refs.form.reset();
            this.loading = false;
          })
        }catch(error){
            this.loading = false;
             this.$swal(error.message);
            // this.text=res.data.message
        }
      }
    },
  },
};
